import Page from 'components/Page';
import React from 'react';
import {
  Button,
  Card,
  CardBody,
  CardHeader,
  Col,
  Form,
  FormFeedback,
  FormGroup,
  FormText,
  Input,
  Label,
  Row,
  Alert,
  Table,
} from 'reactstrap';
import states from "../api/states";
import PageSpinner from 'components/PageSpinner';
import { newDeduction,findDeductions,editDeduction,findDeductionsScheduled,deleteDeduction } from "../api/api";
import AsyncStorage from '@callstack/async-storage';
import conf from "../api/config";
import MUIDataTable from "mui-datatables";
import CustomToolbarSelectExpenses from "../components/CustomToolbarSelectExpenses";
class ExpenseDeductions extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            expense : 'nn',
            deducted : '',
            payment_date : '',
            xtoken:'',
            msg: '',
            error: '',
            downloadFile:true,
            preload:false,
            showmore:true,
            data: [],
            expenses:[],
        };
        AsyncStorage.getItem(conf.secret)
        .then((results) => {
          if( results !== null )
          {
            let obje_results = JSON.parse(results);
            this.setState({
              ...this.state,
              xtoken : obje_results.user.token,
            });
            findDeductions(obje_results.user.token).then((response) => {
                this.setState({
                    ...this.state,
                    data:response.data,
                });
            }).catch((respError) => {});
            
            findDeductionsScheduled(obje_results.user.token).then((response) => {
                this.setState({
                    ...this.state,
                    expenses:response.data,
                });
            }).catch((respError) => {});

          }
        })
        .catch((error) => {
          console.log('storage error', error);
        });
    }
    componentDidMount() {
    }


    firstUpper = (str) => {
        let string = str.toLowerCase();
        return string.charAt(0).toUpperCase() + string.slice(1);
    }

    rerenderParentCallback = () => {
      findDeductions(this.state.xtoken).then((response) => {
        this.setState({
            ...this.state,
            data:response.data,
        });
      }).catch((respError) => {
      });
    }
    
    handleExpenseInput = (event) => {
      const v = event.target.value;
      this.setState({...this.state, expense: v,});
    }
    handleDeductedInput = (event) => {
        const v = event.target.value;
        this.setState({...this.state, deducted: v,});
    }
    handlePaymentDateInput = (event) => {
        const v = event.target.value;
        this.setState({...this.state, payment_date: v,});
    }

    handleSaveChanges = () => {
        this.setState({ ...this.state, preload:true});
        newDeduction(this.state.xtoken, this.state)
        .then((response) => {
            // console.log('rrrrr', response);
            if( response.status === 200)
            {
                this.setState({
                    ...this.state,
                    expense : 'nn',
                    deducted : '',
                    payment_date : '',
                    msg: response.message,
                    data: response.data,
                    preload:false,
                    error: '',
                });
                this.forceUpdate();
            }
            else
            {
                this.setState({
                    ...this.state,
                    error: response.message,
                    preload:false,
                    msg: '',
                });
            }
        })
        .catch((error) => {
            this.setState({
                ...this.state,
                error: 'Connection error occured',
                preload:false,
            });
            console.log('error', error);
        });
    }
    render() {
        const columns = [
            {
                name: 'Expense',
                options: {
                  display: true,
                }
            },
            {
              name: 'Payment Date',
              options: {
                display:true,
                customBodyRender: (value, tableMeta, updateValue) => (
                    <Label>{value}</Label>
                )
              }
            },
            {
              name: 'This installment',
              options: {
                display:true,
                customBodyRender: (value, tableMeta, updateValue) => (
                    <Label>${value}</Label>
                )
              }
            },
            {
                name: 'Total deduction',
                options: {
                    display:true,
                    customBodyRender: (value, tableMeta, updateValue) => (
                        <Label>${value}</Label>
                    )
                }
            },
            {
                name: 'Sum paid',
                options: {
                    display:true,
                    customBodyRender: (value, tableMeta, updateValue) => (
                        <Label>${value}</Label>
                    )
                }
            },
            {
                name: 'Balance',
                options: {
                    display:true,
                    customBodyRender: (value, tableMeta, updateValue) => (
                        <Label>${value}</Label>
                    )
                }
            },
        ];

        const data2 = () => {
          let rtn = [];
          this.state.data.map(item => {
            rtn.push([
              item.explabel,
              item.datelabel,
              item.deducted,
              item.total,
              item.paid_sf,
              item.bal,
              item.payment_date,
            ]);
          });
          return rtn;
        }
      
        const options = {
            filter: true,
            selectableRows: 'none',
            selectableRowsOnClick:true,
            filterType: 'dropdown',
            responsive: 'vertical',
            draggableColumns: {
              enabled: true
            },
            rowsPerPage: 5,
            selectToolbarPlacement: 'replace',
            customToolbarSelect: (selectedRows, displayData, setSelectedRows) => (
              <CustomToolbarSelectExpenses rerenderParentCallback={this.rerenderParentCallback} selectedRows={selectedRows} displayData={displayData} setSelectedRows={setSelectedRows}  originalData={this.state.data} Xtoken={this.state.xtoken}/>
            ),
            downloadOptions: {
                filename: 'all-expenses-excel-format.csv',
                separator: ';',
                filterOptions: {
                  useDisplayedColumnsOnly: true,
                  useDisplayedRowsOnly: true,
                }
            },
            onDownload: (buildHead, buildBody, columns, data2) => {
              if (this.state.downloadFile) {
                let val= `${buildHead(columns)}${buildBody(data2)}`.trim();
                return val;
              }
      
              return false;
            },
            onRowSelectionChange: (currentRowsSelected, allRows, rowsSelected) => {
              // console.log('currentRowsSelected', currentRowsSelected);
            },
            onRowsDelete: rowsDeleted => {
              // console.log(rowsDeleted, 'were deleted!');
            },
            onChangePage: numberRows => {
              // console.log(numberRows);
            },
            onSearchChange: searchText => {
              // console.log(searchText);
            },
            onColumnSortChange: (column, direction) => {
              // console.log(column, direction);
            },
            onViewColumnsChange: (column, action) => {
              // console.log(column, action);
            },
            onFilterChange: (column, filters) => {
              // console.log(column, filters);
            },
            onCellClick: (cellIndex, rowIndex) => {
              // console.log(cellIndex, rowIndex);
            },
            onRowClick: (rowData, rowState) => {
              // console.log(rowData, rowState);
            },
        };
      return (
        <Page title="Deductions" breadcrumbs={[{ name: 'Deductions', active: true }]}>
          <Row>
            <Col xl={12} lg={12} md={12}>
              <Card>
                <CardHeader>
                    Create deduction
                    {this.state.preload && (<PageSpinner/>)}
                </CardHeader>
                {this.state.error.length > 0 && (
                    <Alert color="danger">{this.state.error}</Alert>
                )}
                {this.state.msg.length > 0 && (
                    <Alert color="success">{this.state.msg}</Alert>
                )}
              </Card>
            </Col>
          </Row>

          <Row>
            <Col xl={4} lg={12} md={12}>
              <Card>
                <CardBody>
                  <Form>
                    <FormGroup>
                      <Label for="exampleSelect">Expense*</Label>
                      <Input onChange={this.handleExpenseInput} value={this.state.type} defaultValue={this.state.expense} type="select" name="type">
                        <option value="nn" disabled={true}>Select expense</option>
                        {
                            this.state.expenses.map((stt, idx) => (
                                <option key={idx} value={stt.id}>{stt.label}</option>
                            ))
                        }
                      </Input>
                    </FormGroup>
                    <FormGroup>
                      <Label for="examplePassword">Deduction amount($)*</Label>
                      <Input onChange={this.handleDeductedInput}
                        type="number"
                        value={this.state.deducted}
                        name="origin"
                        placeholder=""
                      />
                    </FormGroup>  
                    <FormGroup>
                      <Label for="examplePassword">Deduction date*</Label>
                      <Input onChange={this.handlePaymentDateInput}
                        type="date"
                        value={this.state.payment_date}
                        name="origin"
                        placeholder=""
                      />
                    </FormGroup> 
                    <FormGroup check row>
                      <Col sm={{ size: 10, offset: 2 }}>
                        <Button type="button" onClick={this.handleSaveChanges}>Add deduction</Button>
                      </Col>
                    </FormGroup>
                  </Form>
                </CardBody>
              </Card>
            </Col>

            
    
            <Col xl={8} lg={12} md={12}>
              <Card>
                <CardBody>
                    <MUIDataTable title={'Expenses'}
                    data={this.state.data.map(item => {
                      return [
                        item.explabel,
                        item.datelabel,
                        item.deducted,
                        item.total,
                        item.paid_sf,
                        item.bal,
                        item.payment_date,
                      ]
                    })} columns={columns} options={options} />
                </CardBody>
              </Card>
            </Col>
        </Row>
    
        </Page>
      );
    }
}
export default ExpenseDeductions;
